footer {
	background: var(--color-primary);
	padding: 3rem 0;
	text-align: center;
	font-size: 0.9rem;
	margin-top: 6rem;
}

footer blockquote {
	color: var(--color-bg);
}
.footer__logo {
	font-size: 2rem;
	font-weight: 500;
	margin-bottom: 2rem;
	display: inline-block;
	cursor: default;
}

.permalinks {
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	gap: 2rem;
	margin: 0 auto 3rem;
}

.permalinks li a {
	color: var(--color-bg-variant-2);
}
.permalinks li a:hover {
	color: var(--color-white);
}

.footer__socials {
	display: flex;
	justify-content: center;
	gap: 1rem;
	margin-bottom: 4rem;
}

.footer__socials a {
	background: var(--color-bg);
	color: var(--color-white);
	padding: 0.8rem;
	border-radius: 0.7rem;
	display: flex;
	border: 1px solid transparent;
}

.footer__socials a:hover {
	background: transparent;
	color: var(--color-bg);
	border-color: var(--color-bg);
}

.footer__copyright {
	margin-bottom: 4rem;
	color: var(--color-bg);
}

blockquote::before {
	content: open-quote;
}
blockquote::after {
	content: close-quote;
}
blockquote {
	quotes: '“' '”' '‘' '’';
}

/************************ MEDIA QUERIES *********************/

/* Medium Devices */

@media screen and (max-width: 1024px) {
}

/* Small Devices */

@media screen and (max-width: 600px) {
	.permalinks {
		flex-direction: column;
		gap: 1.5rem;
	}
	.footer__socials {
		margin-bottom: 2.6rem;
	}
}
